import { Fragment } from "react";

import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import { Divider } from "@mui/material";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import ArticleIcon from "@mui/icons-material/Article";
import Box from "@mui/material/Box";

import AppBar from "./Components/AppBar";

import tableNameData from "./tableNameData.json";

const renderTableNameDatum = (tableNameDatum) => {
  const { tableName, displayName, crfVersions } = tableNameDatum;

  return (
    <Fragment key={tableName}>
      <Divider></Divider>
      <Box
        sx={{
          height: 20,
        }}
      />

      <Typography variant="h6">{displayName}</Typography>

      <List>
        {crfVersions.map((crfVersion) => {
          return renderLink(crfVersion);
        })}
      </List>
      <Box
        sx={{
          height: 10,
        }}
      />
    </Fragment>
  );
};

const renderLink = (crfVersion) => {
  const { fileName, newFileName } = crfVersion;
  return (
    <ListItem key={newFileName}>
      <ListItemButton component="a" href={`./pdfs/${newFileName}`}>
        <ListItemIcon>
          <ArticleIcon />
        </ListItemIcon>
        <ListItemText primary={fileName} />
      </ListItemButton>
    </ListItem>
  );
};

function App() {
  const queryParams = new URLSearchParams(window.location.search);
  const tableName = queryParams.get("tableName");
  const filteredTableNameData = tableNameData.filter(
    (d) => d["tableName"] === tableName
  );

  if (filteredTableNameData.length > 0) {
    return (
      <Fragment>
        <AppBar></AppBar>
        <Container>
          {filteredTableNameData.map((tableNameDatum) =>
            renderTableNameDatum(tableNameDatum)
          )}
        </Container>
      </Fragment>
    );
  } else {
    return (
      <Fragment>
        <AppBar></AppBar>
        <Container>
          {tableNameData.map((tableNameDatum) =>
            renderTableNameDatum(tableNameDatum)
          )}
        </Container>
      </Fragment>
    );
  }
}

export default App;
